  }
}
@media (min-width: 1440px) {
  .main-header--index {
    position: relative;
    width: 100%;
  }
  .main-header--index::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 694px;
    top: 0;
    left: 0;
    background: url("../img/index-background-desk.jpg"), linear-gradient(to right, #ffffff 50%, #63a73a 50%);
    background-repeat: no-repeat;
    background-position: right top;
    z-index: 1;
  }

  .main-header__wrapper {
    width: 1220px;
  }

  .main-header__logo {
    margin-top: 53px;
    width: 202px;
    height: 59px;
    z-index: 2;
  }
}
.main-nav {
  position: relative;
  width: auto;
}

.main-nav__toggle {
  position: absolute;
  top: -66px;
  right: 0;
  z-index: 2;
  display: block;
  width: 64px;
  min-height: 66px;
  background-color: #ffffff;
  cursor: pointer;
  border: none;
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 24px;
  left: 20px;
  width: 24px;
  height: 1.5px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}

.main-nav--closed .main-nav__wrapper {
  display: none;
}

.main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 32px;
  left: 20px;
  width: 25px;
  height: 1.5px;
  background-color: #000000;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}

.main-nav--opened .main-nav__wrapper {
  position: absolute;
  width: 100%;
  background-color: #ffffff;
}

.main-nav--nojs .main-nav__toggle {
  display: none;
}

.main-nav--nojs .main-nav__wrapper {
  position: static;
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.main-nav__item {
  min-height: 66px;
  padding-top: 22px;
  text-align: center;
  border: 1px solid #e6e6e6;
}

.main-nav__link {
  color: inherit;
  text-decoration: none;
  text-transform: uppercase;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
}
.main-nav__link:hover {
  opacity: 0.6;
}
.main-nav__link:active {
  opacity: 0.3;
}

@media (min-width: 768px) {
  .main-nav {
    width: 440px;
  }

  .main-nav--closed .main-nav__wrapper {
    display: block;
  }

  .main-nav__wrapper {
    min-height: 78px;
  }

  .main-nav__toggle {
    display: none;
  }

  .main-nav__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .main-nav__item {
    min-height: 36px;
    margin-top: 32px;
    padding-top: 10px;
    padding-bottom: 10px;
    border: none;
  }
  .main-nav__item--current {
    position: relative;
  }
  .main-nav__item--current::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    height: 2px;
    background: #63a73a;
  }

  .main-nav__link {
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }
}
@media (min-width: 1440px) {
  .main-nav {
    min-width: 478px;
    position: relative;
    z-index: 2;
  }

  .main-nav--opened .main-nav__wrapper {
    background: none;
  }

  .main-nav__item {
    min-height: 30px;
    margin-top: 61px;
    padding-top: 8px;
    padding-bottom: 8px;
  }

  .main-nav__item-secondary {
    color: #ffffff;
  }
  .main-nav__item-secondary--current::after {
    background: #ffffff;
  }

  .main-nav__link {
    font-size: 20px;
    line-height: 30px;
    font-weight: 400;
  }
}
